.scrolllin{
	height: 100%;
	width: 2px;
	position: absolute;
	top:0;
	left: 20px;
	z-index: -1;
	/*margin-top: 6px;*/
}
.scroll_bg{
	height: 100%;
	width: 2px;
	background-color: #CCCCCC;
	position: absolute;
	top:0;
	z-index: -1;
}
.scroll_avance{
	position: absolute;
	width: 2px;
	top:0;
	height: 0;
	background: #00aae4; /* Old browsers */
	background: -moz-linear-gradient(top, #00aae4 0%, #00aae4 90%, #cccccc 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top, #00aae4 0%,#00aae4 90%,#cccccc 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom, #00aae4 0%,#00aae4 90%,#cccccc 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00aae4', endColorstr='#cccccc',GradientType=0 ); /* IE6-9 */
} 
.vscroll{
	position: fixed;
	height: 100%;
	top:52px;
	width: 100%;
	display: table;
	padding: 13px;
	z-index: 41;
}
.vscroll .trow span{
	font-size: 14px;
	padding: 0;
	font-weight: normal;
	display: table-cell;
	padding-left: 20px;
	white-space: nowrap;
	vertical-align: bottom;
	cursor: pointer;
}
.vscroll .trow:hover .ist, .vscroll .trow:hover .itic{
	/* it_st_hover.svg */
	background-image: url(data:image/svg+xml;base64,);
}
.vscroll .trow:hover .ithc {
	background-image: url(data:image/svg+xml;base64,);
}
.ith{
	/* it_th_g.svg */
	background-image: url(data:image/svg+xml;base64,);
	background-repeat: no-repeat;
	background-position: 0 bottom;
}
.iti, .ist{
	/* it_st_g.svg */
	background-image: url(data:image/svg+xml;base64,);
	background-repeat: no-repeat;
	background-position: 3px bottom;
}  
.ith, .iti, .ist{
	color:#666666;
}
.ithc, .itic, .istc{
	color:#009EDE;
}  
.ithc{
	/* it_th.svg */
	background-image: url(data:image/svg+xml;base64,);
}
.itic, .istc{
	/* it_st.svg */
	background-image: url(data:image/svg+xml;base64,);
}
.bgapple{
	position: absolute;
	/* top: 0; */
	top: -52px;
	bottom: 0;
	width: 100%;
	height: 1200px;
	background-color: #eee;
	margin-left: -13px;
	z-index: -4;
}
.tooltip{
	position: fixed;
	right: -50px;
	background-color: #ff6859;
	color: #fff;
	z-index: 87;
	padding: 2px 7px;
	font-size: 11px;
	display: none;
}
.wrapScroll{
	display: table;
	height: 100%;
}