footer{
	width: 100%;
	height: 52px;
	background-color: #fff;
	position: fixed;
	bottom: 0;
	z-index: 55;
}
.fizq{
	width: 100%;
	height: 52px;
	/*foo_izq.svg*/
	background-image: url(data:image/svg+xml;base64,);
	background-position: right center;
	background-repeat: no-repeat;
	cursor: pointer;
}
.fder{
	width: 100%;
	height: 52px;
	/*foo_der.svg*/
	background-image: url(data:image/svg+xml;base64,);
	background-position: left center;
	background-repeat: no-repeat;
	cursor: pointer;
}
.disable{
	cursor: default;
}
.fcen{
	width: 56%;
	color: #666666;
	font-size: 15px;
	text-align: center;
	padding-left: 10px;
	padding-right: 10px;
	line-height: 18px;
}
.fcentx{
	max-width: 56%;
	max-height: 46px;
	overflow: hidden;
	padding-top: 3px;
	padding-bottom: 3px;
	padding-left: 7px;
	padding-right: 7px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	position: absolute;
	top: 16px;
	left: 22%;
	width: 100%;
}

.borg{
	border-top: 3px solid #ccc;
}
.upfooter{
	padding-bottom: 38px !important; 
}
