header.conten.cHom{
	background-color: #fff;
	border-bottom: 1px solid #ccc;
}
header.conten.cHom h1{
	color: #535353;
	text-align: left;
}	
.search{
	border: 1px solid #CCC;
	width: 94%;
	margin: auto;
	background-color: #fff;
	color: #666;
	font-size: 11px;
	text-align: left;
	height: 35px;
	/* serach-bg.svg */
	background-image: url(data:image/svg+xml;base64,);
	background-position: left center;
	background-repeat: no-repeat;
	text-indent: 16px;
	max-width: 400px;
}
.search::-webkit-input-placeholder {
	text-align: left;
}
.search::-moz-placeholder {
	text-align: left;
}
.search:-ms-input-placeholder {
	text-align: left;
}
.search:-moz-placeholder {
	text-align: left;
}
.buscar-btn{
	width: 46px;
	height: 52px;
	/* search.svg */
	background-image: url(data:image/svg+xml;base64,);
	background-repeat: no-repeat;
	background-position: center center;
	cursor: pointer;
}
.buscar-btn.buscar-disabled{
	opacity: 0.5;
	cursor: default;
}
.buscar-btn.close{
	background-image: url("data:image/svg+xml;base64,");
}
.menu{
	width: 46px;
	height: 52px;
	/* menu.svg */
	background-image: url(data:image/svg+xml;base64,);
	background-repeat: no-repeat;
	background-position: center center;
	cursor: pointer;
}
.preguntas{
	width: 46px;
	height: 52px;
	/* preguntas.svg */
	background-image: url(data:image/svg+xml;base64,);
	background-repeat: no-repeat;
	background-position: center center;
	cursor: pointer;
	background-size: 24px;
}
.logo_menu{
	background-color: #00aae4;
	text-align: center;
	padding-left: 10px;
	
	background-repeat: no-repeat;
	background-position: 0 20px;
	width: 58px;
	height: 52px;
	position: absolute;
	cursor: pointer;
}
.logo_menu.tid-logo{
	/* menu-l.svg */
	background-image: url(data:image/svg+xml;base64,);
}
.logo_menu img.ieslogo{
	max-width: 32px;
	margin-top: 21px;
}
img.disabled{
	opacity: .4;
}
.disabled{
	background-color: rgb(0, 170, 228, .8);
	background-image: none !important;
	cursor: default;
}
.logo_menu img{
	margin-top: 20px;
}
.buttonstop{
	position: absolute;
	top:0;
	right: 0;
	display: flex;
}
.buttonstop .search{
	width: 200px;
	display: inline-flex;
	position: absolute;
    right: 43px;
    top: 9px;
}
.buttonstop div{
	display: inline-flex;
}
