#panelder{
    display: flex;
    background-color: #eee;
    width: 100%;
    color: #535353;
    overflow: hidden;
}
#panelder .back{
    right: inherit;
    left: 0;
}
/* .pandercont p{
    font-size: 12px;
    font-style: italic;
    padding: 15px;
    text-align: center;
  } */
.headpan{
    width: 100%;
    color: #FFFFFF;
    background-color: #009DDD;
    height: 52px;
    line-height: 52px;
  }

  .headpan{
    position: fixed;
    z-index: 199;
  }
  .wraperpan{
    position: relative;
  }


  .pander{
    width: 100%;
    position: fixed;
    z-index: 190;
    border-bottom: 1px solid #eee;
  }
  .pander tr td{
    background-color: #eee;
    width: 49%;
    height: 57px;
    border-left: #F2F2F2 2px solid;
    background-repeat: no-repeat;
    background-position: center 14px;
    position: relative;
    text-align: center;
    vertical-align: bottom;
    font-size: 10px;
    padding-bottom: 4px;
    cursor: pointer;
  }
  .pander tr td:first-child, .itnotas li{
    border-left: none;
    /*background-image: url("../im/pd_not.svg");*/
    background-image: url(data:image/svg+xml;base64,);
  }
  .pander tr td:last-child, .itresum li{
    /*background-image: url("../im/pd_res.svg");*/
    background-image: url(data:image/svg+xml;base64,);
  }
  .pander tr td.disable{
    cursor: default;
    opacity: .3;
  }
  .submenu_pander{
    position: fixed;
    bottom: 0;
    width: 100%;
    right: 0;
    background-color: #EEEEEE;
    color: #666666;
    z-index: 60;
    box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);
}

  
  
  .head_subm{
    font-weight: 900;
    text-align: center;
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #C8C8C8;
    position: relative;
  }
  .btn_sm{
    display: block;
    width: 100%;
    height: 58px;
    text-indent: 67px;
    background-position: 29px center;
    background-repeat: no-repeat;
    text-align: left;
    background-color: #FFFFFF;
  }
  .btn_sm:hover{
    background-color: #eee;
  }
  .bgdesc{
    /* background-image: url(../im/sub_desc.svg); */
    background-image: url(data:image/svg+xml;base64,);
  }
  .bgelim{
    /* background-image: url(../im/sub_elim.svg); */
    background-image: url(data:image/svg+xml;base64,);
  }

  .panmat {
    font-weight: 400;
    margin: 0;
    height: 52px;
    line-height: 52px;
    font-size: 15px;
    padding-left: 10px;
    overflow: hidden;
    white-space: nowrap;
    position: absolute;
    left: 58px;
    top: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding-right: 50px;
}

  .factivo{
    width: 100%;
    height: 1px;
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: #ff6859;
    display: none;
  }

  
.pander tr td:first-child.activo, .pander tr td:first-child:hover{
    border-left: none;
    /* background-image: url("../im/pd_not_activo.svg"); */
    background-image: url(data:image/svg+xml;base64,);
    color: #ff6859;
  }
  .pander tr td:last-child.activo, .pander tr td:last-child:hover{    
    /* background-image: url("../im/pd_res_activo.svg"); */
    background-image: url(data:image/svg+xml;base64,);
    color: #ff6859;
  }

  .pander tr td.activo{
    background-color: #fff;
  }
  
  .itnotas, .itresum{
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .itnotas li, .itresum li{
    display: block;
    
    padding: 23px 10px 23px 30px;
    background-color: #fff;
    border-bottom: 1px solid #E6E6E6;
    background-repeat: no-repeat;
    background-position: 20px 50%;
    text-indent: 30px;
    background-size: 15px;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .itnotas li.none, .itresum li.none {
    background-image: none;
    border-bottom: 0;
    color: #7f7f7f;
  }
  
  
@media screen and (min-width: 960px) {
    .itresum li:hover, .itnotas li:hover{
        background-color: #eee;
    }
}