
.indicador{
    position: absolute;
    z-index: 99;
    height: 50px;
    background-color: #fff;
    -moz-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
    -webkit-transition: all 0.2s ease-in;
    -ms-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
    box-shadow: -10px 5px 20px 5px rgba(0, 0, 0, 0.5);    
  }
  
  .indicador button:after{
    content: "";
    background-color: #b2b2b2;
    width: 1px;
    height: 30px;
    position: absolute;
    left: 0;
    top: 10px;
  }
  .indicador button:first-child:after{
    background-color: transparent;
  }
  .indicador button{
    position: relative;
  }


  .anotar{
    width: 50px;
    height: 50px;
    /*background-image: url("../im/nota.svg");*/
    background-image: url(data:image/svg+xml;base64,);
    background-position: center center;
    background-repeat: no-repeat;
    position: relative;
  }
  .pintar{
    width: 49px;
    height: 50px;
    /* background-image: url("../im/marcar.svg"); */
    background-image: url(data:image/svg+xml;base64,);
    background-position: center center;
    background-repeat: no-repeat;
  }
  .despintar{
    width: 50px;
    height: 50px;
    /* background-image: url("../im/des-marcar.svg"); */
    background-image: url(data:image/svg+xml;base64,);
    background-position: center center;
    background-repeat: no-repeat;
  }

  @media screen and (min-width: 960px) {
    .indicador button:hover{
      background-color: #E5E5E5;
    }
    .indicador button:before {
      content: "";
      background-color: #b2b2b2;
      width: 1px;
      height: 30px;
      position: absolute;
      right: 0;
      top: 10px;
    }
  }  