
.headpi{
    width: 100%;
    background-color: #00aae4;
    color:#fff;
    font-size: 15px;
    padding: 13px;
  
  }
  .headpi p{
    margin: 9px 0 5px 0;
    line-height: 26px;
  }
  
  .panelizq{
    /*position: fixed;
    height: 150%;
    top:0;
    overflow: hidden;
    */
    display: table;  
    background-color: #fff;
    /*z-index: 57;*/
    line-height: 15px;
    width: 100%;
    color: #535353;
  }
  .panelizq table{ 
  width: 100%;
  }
  .panelizq table tr td{
    height: 68px;
    
  }
  .panelizq table tr td a{
    display: block;
    width: 100%;
    height: 68px;
    line-height: 68px;
    padding-left: 27px;
    text-decoration: none;
    color: #535353;
  }
  /*.panelizq table tr td:last-child{
    padding: 16px 16px 16px 0;
  }*/
  .panelizq table tr td:first-child{
    width: 53px;
    text-align: center;
  }
  .panelizq table tr td:last-child{
    padding-right: 12px;
  }
  .panelizq table tr{
    /*border-bottom: 1px solid #CCCCCC;*/
    cursor: pointer;
  }
  .panelizq table tr.bo{
    border-bottom: 1px solid #CCCCCC;
  }
  .panelizq table tr td.sim{
    text-align: left !important;
    padding-left: 27px;
  }
  #tabpaniz{
    overflow-y: auto;
    position: relative;
  }
  /*
  .panelizq table tr:first-child{
    background-color: #00aae4;
    color:#fff;
    border-bottom: none;
  }*/
  
  .panelizq table tr td.nopad{
    padding: 0;
  }
  
  .panelizq table tr.nocursor{
    cursor: default;
  }
  .panelizq table tr.nocursor:hover{
    background-color: transparent;
  }
  .timen{
    letter-spacing: 4px !important;
    font-size: 12px !important;
    padding-left: 21px !important;
    color: #999 !important;
    height: 40px;
    background-color: #EEEEEE;
  }
  .menuWrap{
    height:100%;
    overflow-y: auto;
  }

  tr.active{
    background-color: #E5E5E5;
  }

  .indice{
    list-style: none;
    padding: 0;
    margin: 0;
    background-color: #EEEEEE;
  }
  .indice li{
      display: block;
      width: 100%;
      padding: 12px 10px 12px 30px;
      color: #535353;
      margin-bottom: -1px;
      /* item.svg */
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      background-position: right center;
      cursor: pointer;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      padding-right: 36px;
  }
  .indice li.visto{
      /* check.svg */
      background-image: url(data:image/svg+xml;base64,);
  }
  .indice li.indice_n1{
    font-weight: 900;
    border-bottom: 1px solid #C8C8C8;
    border-top: 1px solid #C8C8C8;
  }
  .indice li.indice_n2{
    position: relative;
    text-indent: 21px;
  }
  .indice li.indice_n2:after{
    content: "";
    background-color: #009DDD;
    width: 13px;
    height: 2px;
    position: absolute;
    left: 30px;
    top: 21px;
  }
  .indice li.indice_n3{
    position: relative;
    text-indent: 42px;
  }
  .indice li.indice_n3:after{
    content: "";
    background-color: #ff6859;
    width: 13px;
    height: 2px;
    position: absolute;
    left: 54px;
    top: 21px;
  }
  .indice li.activo{
    background-color: #E5E5E5;
   }